<template>
  <div class="data container-fluid">
    <div class="row">
      <div class="col-md-6 draw-item" id="category"></div>
      <div class="col-md-6 draw-item" id="likes">2</div>
      <div class="col-md-6 draw-item" id="views">2</div>
      <!-- <div class="col-md-6 draw-item" id="create"></div> -->
      <div class="col-md-6 draw-item" id="user-add"></div>
    </div>
  </div>
</template>

<script>
import draw from "../utils-admin/mixindraw";
export default {
  mixins: [draw],
  activated() {
    this.getCategoryData();
    this.getStatistics();
  },
  methods: {
    async getCategoryData() {
      //category
      this.showLoading(["#category"]);
      let data = this.$store.state.blog.drawCategory;
      if (!data.length) {
        data = await this.$store.dispatch("getTypes_num");
      }
      let category = data.map(v => v.name);
      let category_item_num = data.map(v => v.num);
      let category_item_like = data.map(v => v.likes_num);
      let option = {
        title: {
          text: "分类数据"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["博客数量", "收藏数量"]
        },
        xAxis: {
          type: "category",
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "博客数量",
            type: "bar",
            stack: "总量",
            label: {
              show: true
            },
            data: category_item_num
          },
          {
            name: "收藏数量",
            type: "bar",
            stack: "总量",
            label: {
              show: true
            },
            data: category_item_like
          }
        ]
      };
      this.MyDraw(option, "#category");
    },
    // views likes createAt
    async getStatistics() {
      this.showLoading(["#views", "#likes", "#user-add"]);
      let data = this.$store.state.blog.statistics;
      if (!data.length) {
        data = await this.$store.dispatch("getStatistics");
      }
      let { likes, views, createdAt, counts: user_count } = data;
      this.setLikesOptions(likes);
      this.setViewsOptions(views);
      this.setUserCountOptions(user_count);
      // this.setCtreateAtOptions(createdAt);
    },
    setViewsOptions(preOption) {
      let category = preOption.map(v => v.title);
      let category_item_num = preOption.map(v => v.views);
      let option = {
        title: {
          text: "博客浏览排行"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["观看数量"]
        },
        xAxis: {
          type: "category",
          show: false,
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "观看数量",
            type: "bar",
            label: {
              show: true
            },
            data: category_item_num
          }
        ]
      };
      this.MyDraw(option, "#views");
    },
    setLikesOptions(preOption) {
      let category = preOption.map(v => v.title);
      let category_item_num = preOption.map(v => v.likes_length);
      let option = {
        title: {
          text: "博客收藏排行"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["收藏数量"]
        },
        xAxis: {
          type: "category",
          show: false,
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "收藏数量",
            type: "bar",
            label: {
              show: true
            },
            data: category_item_num
          }
        ]
      };
      this.MyDraw(option, "#likes");
    },
    setUserCountOptions(preOption) {
      let category = preOption.map(v => v.username);
      let category_item_num = preOption.map(v => v.blog_count);
      let option = {
        title: {
          text: "用户发布数量排行"
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ["发布数量"]
        },
        xAxis: {
          type: "category",
          show: false,
          data: category
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            name: "发布数量",
            type: "bar",
            label: {
              show: true
            },
            data: category_item_num
          }
        ]
      };
      this.MyDraw(option, "#user-add");
    },
    setCtreateAtOptions(preOption) {
      console.log(preOption);
      let data = [];
      var option = {
        title: {
          text: "新发布博客数量"
        },
        tooltip: {
          show: true,
          trigger: "axis"
        },
        xAxis: {
          type: "time",
          splitLine: {
            show: true
          },
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"]
        },
        yAxis: {
          type: "value",
          splitLine: {
            show: true
          }
        },
        series: [
          {
            name: "数量",
            type: "line",
            showSymbol: false,
            hoverAnimation: false,
            areaStyle: {},
            data: [820, 932, 901, 934, 1290, 1330, 1320]
          }
        ]
      };
      this.MyDraw(option, "#create");
    }
  }
};
</script>

<style lang="scss" scoped>
.draw-item {
  min-height: 250px;
  min-width: 250px;
}
</style>